@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700&display=swap');
$f: 'Raleway', sans-serif;
// Global
*{
  margin: 0;
  padding: 0;
}
a{
  text-decoration: none;
}
body{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  &.black::after{
    content: "";
    position: fixed;
    transition: .3s all ease-in-out;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #191b2680;
    z-index: 4;
  }
}
iframe{
  display: block;
  object-fit: cover;
}
li{
  list-style-type: none;
}
// Header
.header{
  background: url("../img/head.webp") center/cover;
  min-height: 440px;
  padding: 20px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  & > a{
    color: #fff;
    font-size: 12px;
    text-decoration: underline;
    position: relative;
    z-index: 2;
  }
  &__logo{
    color: #fff;
    height: 32px;
    position: relative;
    z-index: 2;
    svg{
      height: 32px;
    }
  }
  &::after{
    content: "";
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg,rgba(25,27,38,.32),rgba(25,27,38,.72));
  }
}
// Search 
.searchContainer{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: 2;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
  h1{
    text-align: center;
    font-family: $f;
    letter-spacing: 1.5px;
  }
}
.searchFieldsContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  & > div{
    position: relative;
    i{
      position: absolute;
      top: 50%;
      left: 15px;
      transform: translateY(-50%);
      color: #8f8f8f;
      font-size: 22px;
      cursor: pointer;
    }
  }
  .searchBar{
    width: 500px;
    border: 1px solid #f7f7f7;
    border-radius: 56px;
    box-shadow: 0 16px 40px rgba(25,27,38,.08);
    padding: 15px 5px 15px 44px;
    height: 56px;
    outline: none;
    color: #8f8f8f;
  }
  .selectField{
    background-color: rgba(0, 0, 0, 0.301);
    font-family: $f;
    font-weight: 500;
    font-size: 18px;
    width: 200px;
  }
}
.error{
  color: crimson;
  align-self: center;
  padding-top: 10px;
  font-weight: 700;
  display: none;
}
.main{
  max-width: 1830px;
  margin: 0 auto;
  padding: 50px 32px;
  color: #191b26;
  h2{
    padding-bottom: 15px;
    span{
      font-style: italic;
      font-size: 26px;
      color: #f04b4b;
      margin-right: 5px;
    }
  }
}
.resultsContainer{
  display: flex;
  flex-direction: column;

}
// Results
.resultsGrid{
  columns : 4;
  flex-wrap:wrap;
  gap:20px;
  .result{
    margin-bottom: 20px;
    position: relative;
    box-shadow: 0 1px 3px rgba(0,0,30,.3);
    overflow: hidden;
    &::after{
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: linear-gradient(180deg, rgba(20,20,20,0.7651435574229692) 0%, rgba(34,56,76,0) 49%, rgba(20,20,20,0.742734593837535) 100%);
      opacity: 0;
      transition: .3s all ease-in-out;
    }
    &:hover::after{
      opacity: 1;
    }
    &:hover{
      a,.authorPrev,.tagsPrev,.prevBtn{
        opacity: 1;
      }
    }
    img{
      width: 100%;
      // object-fit: cover;
      display: block;
      // box-shadow: 0 1px 3px rgba(0,0,30,.3);
    }
    .authorPrev,.tagsPrev,.prevBtn{
      position: absolute;
      z-index: 2;
      color: #f7f7f7;
      transition: .2s all ease-in-out;
      opacity: 0;
    }
    .authorPrev{
      bottom: 10px;
      right: 10px;
    }
    .tagsPrev{
      bottom: 10px;
      left: 10px;
    }
    a{
      top: 10px;
      right: 10px;
      position: absolute;
      z-index: 2;
      color: #f7f7f7;
      transition: .2s all ease-in-out;
      opacity: 0;
    }
    i{
      color: #ffffff;
      font-size: 18px;
      background-color: #ffffff25;
      border: 1px solid #fcfafaa6;
      padding: 8px;
      border-radius: 10px;
      transition: .1s all ease-in-out;
      cursor: pointer;
      &:hover{
        border: 1px solid #fcfafaea;
      }
    }
    .prevBtn{
      top: 70%;
      left: 50%;
      transform: translate(-50%,-50%);
      background-color: #f7f7f717;
      border: 1px solid #f7f7f7;
      padding: 10px 20px;
      font-weight: 500;
      border-radius: 25px;
      cursor: pointer;
      transition: .2s all ease;
      &:hover{
        background-color: #f7f7f731;
      }
    }
  }
}
// Filters
.filters{
  display: flex;
  gap: 15px;
  font-size: 12px;
  margin-bottom: 30px;
  & > p{
    border: 1px solid #191b2646;
    border-radius: 10px;
    padding: 6px 12px;
    background-color: #8f8f8f0a;
    cursor: pointer;
    transition: .2s all ease;
    font-family: $f;
    &:hover{
      background-color: #8f8f8f15;
      border: 1px solid #191b26;
    }
    &.filterActive{
      font-weight: 700;
      color: #38385c;
      background-color: #33333346;
    }
  }
}
// Pagination
.results__pag{
  align-self: center;
  display: flex;
  gap: 5px;
  font-size: 20px;
  margin-top: 50px;
  li{
    color: crimson;
    cursor: pointer;
    a{
      color: inherit;
      &.active{
        color: #191b26;
      }
    }
    
  }
}



// ---------------------------------------
// Modal Window
// ---------------------------------------
.modalWindow{
  background-color: #f7f7f7;
  color: #191b26;
  padding: 50px;
  position: fixed;
  width: 60%;
  height: 70%;
  z-index: 5;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 50px;
  font-family: $f;
  overflow: hidden;
  h3{
    padding-bottom: 30px;
  }
  display: none;
  .modalContent{
    display: flex;
    gap: 20px;
    overflow: hidden;
    .modalMedia{
      width: 60%;
      height: 490px;
      box-shadow: 0 1px 3px rgba(0,0,30,.3);
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
      }
    }
  }
}
.closeBtn{
  position: absolute;
  top: 25px;
  right: 35px;
  font-weight: 700;
  font-size: 32px;
  cursor: pointer;
  transition: .2s all ease-in-out;
  &:hover{
    color: #c02e2e;
  }
}
.authorpic{
  display: flex;
  padding: 20px 0px;
  img{
    width: 150px;
    box-shadow: 0 1px 3px rgba(0,0,30,.3);
  }
  gap: 15px;
}
.modalAside{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 400px;
  font-size: 20px;
  & > div{
    align-self: flex-start;

  }
  a:nth-of-type(1){
    margin-top: 100px;
  }
  a{
    margin-top: 20px;
    color: #f8f8f8;
    border: 1px solid #191b26;
    padding: 10px 20px;
    transition: .2s all ease-in-out;
    font-weight: 500;
    background-color: #c02e2e;
    &:hover{
      background-color: darken($color: #c02e2e, $amount: 10);
    }
  }
}
.authorTags,.authorCreator{
  display: flex;
  gap: 8px;
}
.views{
  margin: 15px 0px;
}
